import React from 'react';
import './Classes_Content.css';

function Classes_Content_Back(){
    const boxes = [
        {
            topic:'Python',
            courseName:'  Python ',
            duration:'1',
            description:'/  '},
        {
            topic:'Python',
            courseName:' Python ',
            duration:'1',
            description:'        '},
        {
            topic:'Python',
            courseName:'   ',
            duration:'1',
            description:'      !'},
        {
            topic:'C',
            courseName:'  C',
            duration:'1',
            description:'C , ,    !'},
        {
            topic:'C',
            courseName:'  C',
            duration:'1.5',
            description:'C ,    '},
        {
            topic:'Kotlin',
            courseName:'Google   Kotlin',
            duration:'2',
            description:'Google   Kotlin   '}
    ]
    return (
        <div className="box_cont">
            {boxes.map((box, key)=>(
                <div className="boxes" key={key}>
                    <div className="boxes_content">
                        <span>{box.topic}</span>
                        <div className="boxes_courseName">{box.courseName}</div>
                        <div className="classes_date">
                            {box.duration}
                        </div>
                        <p>{box.description}
                        </p>

                        <hr />
                        <button type="button" className="add_classes">
                            
                        </button>
                    </div>
                </div>
            ))}

        </div>
    );
};


export default Classes_Content_Back;