import '../../Board/Write_form/Write.css'
import '../../App.css';
import {Link, useHistory} from "react-router-dom";
import {useState} from "react";
import File_Upload from "../../Board/Write_form/Upload/File_Upload";


function Study_Board_Update(){

    // 
    let history = useHistory();

    return(
        <div className="write_form">
            <div className="mid_con">
                <form>
                    <fieldset className="form_field">
                        <legend></legend>
                        <ul>
                            <li>
                                <label htmlFor="title_txt"></label>
                                <input type="text" className="inputTitle" placeholder=" " required />
                            </li>
                            <li>
                                <label htmlFor="context_txt"></label>
                                <textarea className="inputContent" placeholder=" " required />
                            </li>
                        </ul>
                        <File_Upload />

                        <div className="button_section">
                            <button type="submit" id="s_button" ></button>
                            <button type="button"  id="c_button" onClick={()=>{
                                history.goBack();
                            }}></button>
                        </div>

                    </fieldset>
                </form>
            </div>



        </div>

    );
}
export default Study_Board_Update;