import React from 'react';
import '../App.css';
import './Css/Study_List.css';
import {Link} from "react-router-dom";



function StudyList(){
    return (

            <div className="container">
                <div className="study_mid_container">
                    <div className="studyPage_con">
                        <div className="studyPage_name">
                        
                        </div>
                        <button type="submit" id="make" value="">
                            <Link to='/Study_Make' className="link">Study </Link>
                        </button>
                    </div>
                    <div className="study_cont_container">
                            <div className="study_mid_container">
                                <table id="study_board">
                                    <thead>
                                        <tr id="board_head">
                                            <th width="10%">No.</th>
                                            <th width="50%%"></th>
                                            <th width="15%"></th>
                                            <th width="15%"></th>
                                            <th width="10%"></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr id="board_body">
                                            <td width="10%">1</td>
                                            <td width="50%">
                                                <Link to="/Study_Inside"  className="link">
                                                    KGIt AWS 
                                                </Link>
                                            </td>
                                            <td width="15%"></td>
                                            <td width="15%">2022-03-18</td>
                                            <td width="10%">12</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                    </div>
                </div>


            </div>



    );
};

export default StudyList;