import * as React from 'react';
import {useForm} from "react-hook-form";
import '../styles/css/SignUp.css'
import {useEffect, useRef, useState} from "react";
import AxiosURL from "../Services/AxiosURL";
import {Link, useHistory} from "react-router-dom";
import bcrypt from 'bcryptjs';
import {responsivePropType} from "react-bootstrap/createUtilityClasses";

// react Hook From 
//   +   
// 1) yarn add react-hook-form //     
// 2) yarn add bcrypt //  
// 3) yarn add redux react-redux redux-actions immer redux-devtools-extension
// 4) yarn add react-modal (     )
// reacthookform  
const SignUp = () => {

    useEffect(() => {
        if(localStorage.getItem('user-info'))
        {
            history.push("/")
        }
    }, []);


    /*const {register, handleSubmit, watch, formState: {errors} } = useForm({mode: "onChange"});
    const onSubmit = (data) => {
        console.log(data);
    } //  Submit  Check
    console.log(watch("emailexample")); //*/

    const { watch, register, formState: {errors}, setError, handleSubmit } = useForm({mode:"onChange"});

    const checkPassword = useRef(); //    useRef hook  password  
    checkPassword.current = watch("password"); //   password_confirm .


    //  submit
    const history = useHistory();

    //  axios DB 
    const onSubmit = (data) => {
        // console.log('data',data);

        AxiosURL.saveMember(data)
            .then((response) => {
                console.log(response.data) // id
                let result = response.data
                localStorage.setItem("user-info",JSON.stringify(result))
                alert(JSON.stringify("  . ")) //   
                history.push("/")
                window.location.reload()
            }).catch(error => {
            console.log(error)
            alert(JSON.stringify("   . ")) //   
        })

    }

    //onClick={()=> {history.push("/signin")}}



    //console.log(watch("email"));
    //console.log(watch("password"));
    //console.log(watch("password_confirm")); //       


    return (
        <div>
            <br/><br/>
            <form id="sendForm" onSubmit={handleSubmit(onSubmit)}>

                <h3>   </h3>
                &nbsp;
                <label>Email</label>
                <input
                    className="signInput"
                    name="email"
                    type="email"
                    placeholder="  ."
                    {...register("email",
                        {required:true, pattern: /^\S+@\S+$/i})}
                />
                {errors.email && <p className="signupPont">ex) studywithus@gmail.com</p>}

                <label>Nickname</label>
                <input
                    className="signInput"
                    name="nickname"
                    placeholder="10  ."
                    {...register("nickname",
                        {required: true, maxLength: 10})}/>
                {errors.nickname && errors.nickname.type === "required" && <p className="signupPont"> .</p>}
                {errors.nickname && errors.nickname.type === "maxLength" && <p className="signupPont"> 10  .</p>}

                <label>BornDate</label>
                <input
                    className="signInput"
                    name="bornDate"
                    type="text"
                    placeholder="ex) 2022-04-15"
                    {...register("bornDate",
                        {required: true})}/>
                {errors.birth && <p className="signupPont">ex) 2022-04-15</p>}

                <label>Password</label>
                <input
                    className="signInput"
                    name="password"
                    type="password"
                    {...register("password",
                        {required: true, minLength: 8})}/>
                {errors.password && errors.password.type === "required" && <p className="signupPont"> .</p>}
                {errors.password && errors.password.type === "minLength" && <p className="signupPont"> 8  .</p>}

                <label>Password Confirm</label>
                <input
                    className="signInput"
                    name="password_confirm"
                    type="password"
                    {...register("password_confirm",
                        {required: true, validate: value => (value === checkPassword.current)  })}/>
                {errors.password_confirm && errors.password_confirm.type === "required" && <p className="signupPont">   .</p>}
                {errors.password_confirm && errors.password_confirm.type === "validate" &&<p className="signupPont">  .</p>}

                <input
                    className="signInput"
                    type="submit"
                ></input>
            </form>

        </div>
    );
};

export default SignUp;