import React from 'react';
import './Classes_Content.css';

function Classes_Cotent_Front () {

    const boxes = [
        {
            topic:'React',
            courseName:'React  ',
            duration:' 2',
            description:'JavaScript   !! React        '},
        {
            topic:'JavaScript',
            courseName:'  JavaScript',
            duration:' 1.5',
            description:'        ! ' +
                'JavaScript   ,  ,  JavaScript     !'},
        {
            topic:'JavaScript',
            courseName:'JavaScript ',
            duration:' 2',
            description:'20  JavaScript 100   '},
        {
            topic:'JavaScript',
            courseName:'JQuery',
            duration:'1',
            description:'jQuery   Ajax  jQuery     !'
                +'jQuery     '},
        {
            topic:'HTML/CSS',
            courseName:'  HTML/CSS ',
            duration:'3 ',
            description:'    HTML/CSS    !'}
    ]




    return (
        <div className="box_cont">
            {boxes.map((box, key)=>(
                <div className="boxes" key={key}>
                    <div className="boxes_content">
                        <span>{box.topic}</span>
                        <div className="boxes_courseName">{box.courseName}</div>
                        <div className="classes_date">
                            {box.duration}
                        </div>
                        <p>{box.description}
                        </p>

                        <hr />
                        <button type="button" className="add_classes">
                            
                        </button>
                    </div>
                </div>
            ))}

        </div>
    );
};


export default Classes_Cotent_Front;