import React, {useState} from 'react';
import {Link, useHistory} from "react-router-dom";
import '../Board/Write_form/Write.css';
function Study_Make() {


    //radio button ============================================
    const [choice, setChoice] = React.useState('public');

    const handlePublicChange =()=>{
        setChoice('public');
    }
    const handlePrivateChange=()=>{
        setChoice('private');
    }
    //=============================================================

    const history = useHistory();



    return (
        <div className="write_form">
            <div className="mid_con">
                <form>
                    <fieldset className="form_field">
                        <legend> </legend>
                        <ul>
                            <li>
                                <label htmlFor="title_txt"> </label>
                                <input type="text" className="inputTitle" placeholder=" " required />
                            </li>
                            <li>

                                <label htmlFor="study_type"> </label>
                                <div className="type_field">
                                    <input type="radio" className="study_radio" value={choice==='public'} name="radio"
                                           onChange={handlePublicChange}/>
                                    <span></span>
                                    <input type="radio" className="study_radio" value={choice==='private'} name="radio"
                                   onChange={handlePrivateChange}/>
                                    <span></span>
                                </div>

                            </li>
                            <li>
                                <label htmlFor="context_txt"> </label>
                                <textarea className="inputContent" placeholder=" " required />
                            </li>
                        </ul>


                        <div className="button_section">
                            <button type="submit" id="s_button" ></button>
                           <button type="button"  id="c_button" onClick={()=> history.goBack()}></button>
                        </div>

                    </fieldset>
                </form>
            </div>



        </div>
    );
};

export default Study_Make;