import {Link} from "react-router-dom";
import React from "react";
import  "../../App.css";

function MyPage_Board(){
    return(

        <table id="main_board">
            <thead>
            <tr id="board_head">
                <th width="10%" className="listHeadNum">No.</th>
                <th width="50%" className="listHeadTitle"></th>
                <th width="15%" className="listHeadAuthor"></th>
                <th width="15%" className="listHeadDate"></th>
                <th width="10%" className="listHeadViews"></th>
            </tr>
            </thead>
            <tbody>
            <tr id="board_body">
                <td width="10%">1</td>
                <td width="50%">
                    <Link to ="#" className="link">
                        .
                    </Link>
                </td>
                <td width="15%"></td>
                <td width="15%">1818-18-18</td>
                <td width="10%">18</td>
            </tr>
            </tbody>
        </table>


    );
}
export default MyPage_Board;